@import './../../../variables';


.Field {
    position: relative;
    overflow: hidden;
    input,
    select {
        font-family: inherit;
        font-size: $font-size-02;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding: 0 $spacing-02;
        transition: background 0.14s, border 0.14s;
        color: $fontColor;
        border: 2px solid transparent;
        border-radius: 4px;
        background: rgba($fontColor, 0.15);
        min-width: 13rem;
        &:focus {
            border: 2px solid $active;
            outline: 0;
            background: rgba($active, 0.15);
        }

        &::-webkit-input-placeholder {
            color: rgba($fontColor, 0.5);
        }

        &:invalid {
            border-color: $danger;
            background: rgba($danger, 0.05);
        }
    }

    select {
        padding: 0 $spacing-01;
    }
}


