@import 'variables';

.firstLetterUpper {
    &:first-letter {
        text-transform: uppercase;
    }
}

.header {
    &-2 {
        margin-top: 0;
        margin-bottom: $spacing-02;
        color: $fontColor;
    }

    &-3 {
        font-size: $font-size-02;
        font-weight: normal;
        margin: 0;
        margin-bottom: $spacing-02;
    }

    &-4 {
        font-size: $font-size-03;
        margin: $spacing-02;
        color: $fontColorLight;
    }
    
    &-5 {
        font-size: $font-size-03;
        font-weight: bold;
        margin: 0;
        color: $fontColor;
    }
    
    &-sub {
        font-size: $font-size-02;
        margin: $spacing-02;
        color: $fontColor;
    }

    &-indent {
        margin-left: $spacing-04;
    }

    &-bold {
        font-weight: bold;
    };
}



.skeleton {
    position: relative;
    overflow: hidden;
    overflow: hidden;
    &::after {
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        content: '';
        transform: translateX(-100%);
        animation: skeletonAnim 2s infinite;
        background-image: linear-gradient(
        90deg,
        rgba(#aaa, 0) 0,
        rgba(#aaa, 0.2) 20%,
        rgba(#aaa, 0.5) 60%,
        rgba(#aaa, 0)
        );
    }

    @keyframes skeletonAnim {
        100% {
            transform: translateX(100%);
        }
    }

    &-articleTile {
        width: 30rem;
        max-width: 30%;
        height: 17.6rem;
        margin-right: $spacing-04;
        background: $bg-color;
    }

    &-article {
        width: 100%;
        height: 3.3rem;
        margin-bottom: $spacing-03;
    }

    &-work {
        width: 100%;
        height: 4.3rem;
        margin-bottom: $spacing-03;
    }

    &-tile {
        height: 4rem;
    }
}

img.img-skeleton {
    animation: shimmer 2s infinite;
    background-image: linear-gradient(
    90deg,
    rgba(#aaa, 0) 0,
    rgba(#aaa, 0.2) 20%,
    rgba(#aaa, 0.5) 60%,
    rgba(#aaa, 0)
    );
    background-size: 400% 400%;
    @keyframes shimmer {
        0% {
            background-position: 0 50%;
        }
        50% {
            background-position: 100% 50%;
        }
        100% {
            background-position: 0 50%;
        }
    }
}

