@import './../../variables';

.Navbar {
    font-size: 1.2rem;
    position: sticky;
    z-index: 2;
    top: 0;
    display: flex;
    align-items: center;
    flex-shrink: 0;
    justify-content: space-between;
    height: $header-height;
    padding: 0 $spacing-02;
    background-color: $bg-color-light;
    box-shadow: $shadow-01;

    &Menu {
        display: flex;
        align-items: center;
        justify-content: center;
    }

    &Logo {
        width: 2rem;
        height: 2rem;
        img {
            width: 100%;
            height: 100%;
        }
    }

    &.NavbarHidden {
        animation: hideNavabr 0.5s forwards;

        @keyframes hideNavabr {
            0% {
                transform: translateY(0);
            }
            99% {
                transform: translateY(-3rem);
            }
            100% {
                visibility: hidden;
                height: 0;
            }
        }
    }
}

.searchBar {
    width: 50vw;
    max-width: 100%;
    margin: 0 $spacing-02;

    input {
        &::-webkit-input-placeholder {
            text-align: center;
        }
    }
}
