@import './../../../variables.scss';


.UserSignature {
    font-size: $font-size-03;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    color: $fontColorAlt;


    .UserAvatar {
        width: 1.5rem;
        height: 1.5rem;
        margin: 0 $spacing-02;
        border-radius: 50%;
        box-shadow: $shadow-02;

        object-fit: cover;
    }

    p {
        font-size: inherit;
        margin: 0;
        color: rgba($fontColorAlt, 0.8);
    }

    &Dark {
        color: $fontColor;
        time{
            color: $fontColorLight;
        }
        
        p {
            color: rgba($fontColor, 0.8);
            font-weight: 500;
        }
    }

    .Company {
        display: flex;
        align-items: center;

        svg {
            margin-right: $spacing-01;
        }
    }

    .separator {
        width: 4px;
        height: 4px;
        border-radius: 2px;
        background: $fontColorLight;
        margin: 0 $spacing-02;
    }

    .userLink {
        display: flex;
        align-items: center;
        border-radius: 3px;
        padding-right: $spacing-01;
        margin: 0 $spacing-01;
        .UserAvatar {
            margin: 0 $spacing-01;
        }

        &:hover {
            background: rgba($active, 0.25);
        }
    }
}


