@import './../../../variables';


.Search {
    position: relative;
    overflow: hidden;
    height: 2rem;
    input {
        font-family: inherit;
        box-sizing: border-box;
        width: 100%;
        height: 100%;
        padding-right: $spacing-07;
        padding-left: $spacing-02;
        transition: border ease-in 0.14s;
        border: 1px solid #d3d3d3;
        border-radius: 4px;
        &:focus {
            border: 1px solid $active;
            outline: 0;
        }
        &::-webkit-search-cancel-button {
            -webkit-appearance: none;
        }

        &::-webkit-input-placeholder {
            color: rgba($fontColor, 0.5);
            font-weight: bold;
        }
    }

    &Icon {
        position: relative;
        flex-shrink: 0;
    }


    &Button {
        font-size: $icon-size-01;
        position: absolute;
        top: 0;
        right: 0;
        display: flex;
        align-items: center;
        justify-content: center;
        width: 2.5rem;
        height: 100%;
        cursor: pointer;
        transition: background 0.24s, color 0.12s;
        color: $fontColor;
        border: none;
        border-radius: 0 4px 4px 0;
        background: transparent;
        &:focus {
            color: $active;
            border: 1px solid $active;
            outline: 0;
        }
    }
}


